.projects__title {
  text-align: center;
  font-weight: 700;
  font-size: 48px;
  line-height: 1.2;
  margin: 0;
}

.projects__container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}

.projects__project {
  /* min-height: 240px; */
  margin: 0;
  padding: 0px;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.projects__image {
  object-fit: cover;
  width: 100%;
  border-radius: 6px;
}

.project__details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  border: 2px solid var(--text-color);
  backdrop-filter: blur(10px);
  font-weight: 400;
  font-size: 18px;
  line-height: 1.2;
  opacity: 0;
  border-radius: 10px;
}

.project__details:hover {
  opacity: 1;
}

.project__details-text {
  text-align: center;
}

.project__details-link,
.project__details-text {
  padding: 5px 10px;
  margin: 5px 0;
  color: var(--text-color);
  text-decoration: none;
}

.project__details-link {
  border: 1px solid var(--text-color);
  border-radius: 6px;
}

.project__details-text:nth-last-of-type(1) {
  font-style: italic;
}

.project__details-link:hover {
  background-color: var(--accent-color);
  color: var(--text-color);
  border-color: transparent;
}

@media screen and (max-width: 1024px) {
  .projects__project {
    min-height: 300px;
  }
  .project__details {
    opacity: 1;
    /* border: 2px solid var(--main-bg-color); */
  }
}

@media screen and (max-width: 768px) {
  .projects__container {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
}

@media screen and (max-width: 670px) {
  .projects__container {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

@media screen and (max-width: 425px) {
  .projects {
    padding: 20px;
  }
  .projects__title {
    font-size: 28px;
  }
}

@media screen and (max-width: 500px) {
  .projects__project {
    min-height: 300px;
  }
}
