/* font-family: 'Big Shoulders Stencil Display', cursive;
font-family: 'Libre Barcode 128 Text', cursive;
font-family: 'Montserrat Alternates', sans-serif; */

:root {
  --main-bg-color: #12161b;
  --text-color: #e6e6e9;
  --subtext-color: #4e4e50;
  --accent-color: #5b84a4;
  --icon-color: #e6e6e9;
}

.app {
  background-color: #12161b;
  background-color: var(--main-bg-color);
  color: #e6e6e9;
  color: var(--text-color);
  min-height: 100vh;
  background-image: url(../../static/media/circles.a7474d8e.svg);
  background-size: cover;
  background-position: center;
  position: relative;
  z-index: 0;
}

.app::before {
  content: "";
  position: fixed;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: transparent;
  background: radial-gradient(
    circle,
    transparent 0%,
    transparent 20%,
    #12161b 80%
  );
  background: radial-gradient(
    circle,
    transparent 0%,
    transparent 20%,
    var(--main-bg-color) 80%
  );
  z-index: -1;
}

.app * {
  box-sizing: border-box;
  /* outline: 1px dashed var(--accent-color); */
  transition: all 0.2s ease-in-out;
  font-family: 'Montserrat Alternates', sans-serif;
}

.app *::selection {
  background-color: #5b84a4;
  background-color: var(--accent-color);
  color: #12161b;
  color: var(--main-bg-color);
}

@media screen and (max-width: 768px) {
  .app {
    padding: 30px 50px;
    background-size: 800%;
    background-position: center;
  }
}

@media screen and (max-width: 480px) {
  .app {
    padding: 20px;
  }
}

.header {
  width: 100%;
  /* max-width: 1440px; */
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 auto;
  z-index: 9999;
  position: relative;
}

.header__list {
  list-style: none;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  margin: 0;
  width: 100%;
}

.header__link {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  text-decoration: none;
  color: var(--text-color);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px 24px;
  box-shadow: 0 0 0 1px transparent, 0 0 0 1px var(--subtext-color);
  border-radius: 6px;
  position: relative;
}

.header__link-effect {
  margin-left: 10px;
  width: 10px;
  height: 10px;
  -webkit-transform: rotate(45deg) translate(0, -50%);
          transform: rotate(45deg) translate(0, -50%);
  border-right: 1px solid var(--subtext-color);
  border-bottom: 1px solid var(--subtext-color);
}

.header__list-item {
  margin-right: 20px;
}

.header__list-item:nth-last-child(1) {
  margin-right: 0;
}

.header__link:hover {
  box-shadow: 0 0 0 1px var(--text-color), 0 0 0 2px var(--text-color);
  color: var(--text-color);
}

.header__link:hover .header__link-effect {
  border-right: 2px solid var(--text-color);
  border-bottom: 2px solid var(--text-color);
  -webkit-transform: rotate(45deg) translate(20%, -30%);
          transform: rotate(45deg) translate(20%, -30%);
}

.header__burger-menu {
  display: none;
  width: 44px;
  background-color: transparent;
  border: 0;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
}

.header__burger-menu:hover {
  cursor: pointer;
  opacity: 0.8;
}

.header__burger-menu > span,
.header__burger-menu > span::before,
.header__burger-menu > span::after {
  display: block;
  position: absolute;
  width: 28px;
  height: 3px;
  background-color: var(--text-color);
}
.header__burger-menu > span::before {
  content: "";
  top: -9px;
}
.header__burger-menu > span::after {
  content: "";
  top: 9px;
}

.header__menu {
  display: none;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: var(--main-bg-color);
  background-image: url(../../static/media/circles.a7474d8e.svg);
  background-size: cover;
  background-position: center;
}

.header__menu-button {
  color: var(--text-color);
  text-decoration: none;
  font-size: 32px;
  line-height: 1.2;
  margin-bottom: 20%;
}

.header__menu-button:nth-last-child(2) {
  margin: 0;
}

.header__menu-close {
  position: absolute;
  font-size: 32px;
  line-height: 1.2;
  color: var(--text-color);
  top: 30px;
  right: 30px;
  background-color: transparent;
  border: 0;
}

.header__title {
  font-weight: 400;
  font-size: 42px;
  line-height: 1.2;
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: "Libre Barcode 128 Text", cursive;
}

@media screen and (max-width: 768px) {
  .header__menu {
    display: -webkit-flex;
    display: flex;
  }
  .header__overlay {
    display: block;
  }
  .header__list {
    display: none;
  }
  .header__link {
    font-size: 28px;
  }
  .header__list-item {
    padding: 20px;
  }
  .header__logo {
    width: 50px;
    -webkit-animation: rotateLogo 60s linear infinite;
            animation: rotateLogo 60s linear infinite;
  }
  .header__burger-menu {
    display: -webkit-flex;
    display: flex;
  }
}

@-webkit-keyframes breath {
  0% {
    border-radius: 45% 55% 50% 50% / 30% 45% 60% 75%;
  }
  10% {
    border-radius: 40% 65% 40% 65% / 45% 30% 70% 60%;
  }
  20% {
    border-radius: 50% 55% 50% 50% / 50% 45% 55% 50%;
  }
  30% {
    border-radius: 35% 65% 35% 65% / 60% 35% 65% 40%;
  }
  40% {
    border-radius: 40% 60% 45% 55% / 70% 30% 70% 30%;
  }
  50% {
    border-radius: 75% 25% 60% 40% / 40% 60% 45% 60%;
  }
  60% {
    border-radius: 40% 60% 45% 55% / 70% 30% 70% 30%;
  }
  70% {
    border-radius: 35% 65% 35% 65% / 60% 35% 65% 40%;
  }
  80% {
    border-radius: 50% 55% 50% 50% / 50% 45% 55% 50%;
  }
  90% {
    border-radius: 40% 65% 40% 65% / 45% 30% 70% 60%;
  }
  100% {
    border-radius: 45% 55% 50% 50% / 30% 45% 60% 75%;
  }
}

@keyframes breath {
  0% {
    border-radius: 45% 55% 50% 50% / 30% 45% 60% 75%;
  }
  10% {
    border-radius: 40% 65% 40% 65% / 45% 30% 70% 60%;
  }
  20% {
    border-radius: 50% 55% 50% 50% / 50% 45% 55% 50%;
  }
  30% {
    border-radius: 35% 65% 35% 65% / 60% 35% 65% 40%;
  }
  40% {
    border-radius: 40% 60% 45% 55% / 70% 30% 70% 30%;
  }
  50% {
    border-radius: 75% 25% 60% 40% / 40% 60% 45% 60%;
  }
  60% {
    border-radius: 40% 60% 45% 55% / 70% 30% 70% 30%;
  }
  70% {
    border-radius: 35% 65% 35% 65% / 60% 35% 65% 40%;
  }
  80% {
    border-radius: 50% 55% 50% 50% / 50% 45% 55% 50%;
  }
  90% {
    border-radius: 40% 65% 40% 65% / 45% 30% 70% 60%;
  }
  100% {
    border-radius: 45% 55% 50% 50% / 30% 45% 60% 75%;
  }
}

